//
//              3,   Layout 
//--------------------------------------------

.footer{
    clear: both;
    text-align: center;
    position: relative;
    bottom: 0px;
    width: 100%;

        @include media(map-get($breakpoints, base)){position: absolute;};

    p{
        font-size: map-get($map: $font-size, $key: sm);
        text-align: center;
        display: inline-block;
        width: 100%;
        color: map-get($map: $color, $key: primary--light);

            @include media(map-get($breakpoints, base)){
                width: 70%;
                text-align: left;
        };
    }
}

.footer--portfolio{
    position: relative;

            @include media(map-get($breakpoints, base)){position: relative};
}