//
//              3,   Layout 
//--------------------------------------------

.header{
    background-color: map-get($map: $grey, $key: white);
    padding: 0rem 1rem 3rem 1rem;
    overflow: hidden;
    position: relative;

            @include media(map-get($breakpoints, base)){
                padding-bottom: 0rem;

                    @include media(map-get($breakpoints, huge)){
                        @include centerDiv--x;
                        max-width: 90%;      
                };
            };

        // logo in header (left side) //
        .header__logo{
            display: inline-block;
            max-width: 50%;
        }

        .logo__title{
            @include centerDiv--x;
            padding: 2.5rem 1rem 1.5rem 1rem;
            margin: 0px;
            font-size: map-get($map: $font-size, $key: m);
            color: white;
            font-weight: normal;
            background-color: map-get($map: $color, $key: primary--dark);
            position: relative;
           
                @include media(map-get($breakpoints, large)){padding: 1.5rem 1rem;};
        }

        // Navigation divs and menus (right side)//
        .header__wrapper{
            display: inline-block;
            padding: 1.5rem 1rem 0rem 0rem;
            position: absolute;
            right: 1rem;
            
                @include media(map-get($breakpoints,base)){bottom: 1rem;};
        }

        .icon-navicon--burger-menu{
            float: right;
            font-size: 40px;
            color: map-get($map: $color, $key: primary--dark);
            z-index: 11;
            position: relative;
            cursor: pointer;

                @include media(map-get($breakpoints,base)){display: none;};
        }

        .icon-delete-1{
            font-size: 20px;
            position: relative;
            margin: 12.5px;
            cursor: pointer;
    }
}