//
//               4,   Pages
//--------------------------------------------

.content--showcase-page{
    color: map-get($map: $color, $key: primary);

    .showcase__outer-container{
        padding: 0rem 1.5rem 1.5rem 1.5rem;

            @include media(map-get($breakpoints, base)){
                @include centerDiv--x;
                max-width: 1000px;
                position: relative;
                padding: 3rem 1rem 0rem 1rem;
        }
    }

    .showcase__inner-container-textbox{padding: 2rem 0rem;}

    .inner-container__title{
        font-size: map-get($map: $font-size, $key: L);
        margin-bottom: 1rem;
    }

    .inner-container__text{
        @include inner-article--text-style;
        line-height: 1.3rem;
    }

    .showcase__inner-container-imgbox{
        padding: 0rem 1.5rem;

            @include media(map-get($breakpoints, base)){padding: 0rem;}
    }

    .showcase__img-box{
        @include size(100%, 280px);
        background-image: url('../images/showcase-img4.jpg');
        background-repeat: no-repeat;
        background-size: cover;
        background-position: center;
        position: relative;
        overflow: hidden;
        margin: 1rem 0rem;

            @include media(map-get($breakpoints, base)){
                max-width: 31.5%;
                max-height: 320px;
                display: inline-block;
                border: none;
                padding: 0rem 0.5rem;
                text-align: center;
                
        }
    }

    .showcase__img-box[data-id ='imgBoxTwo']{
        background-image: url('../images/showcase-img6.jpg');
        background-position: left;

            @include media(map-get($breakpoints, base)){
                margin: 1rem 0.5rem;

                    @include media(map-get($breakpoints, large)){margin: 1rem;}
        }
    }

    .showcase__img-box[data-id ='imgBoxThree']{background-image: url('../images/showcase-img8.jpg')}

    .img-box__columns{
        @include size(18.5%, 100%);
        z-index: 3;
        display: inline-block;
        position: relative;
    }

    .img-box__text{
        @include position(absolute, null, null, -140px, 0px);
        background: map-get($map: $grey, $key: white);
        text-align: center;
        width: 100%;
        padding: 0.5rem 2rem;
        z-index: 1;

            h4{
                font-size: map-get($map: $font-size, $key: sm);
                margin: 0.5rem 0rem;

                    @include media(map-get($breakpoints, base)){font-size: map-get($map: $font-size, $key: m);}
            }

            p{
                font-size: map-get($map: $font-size, $key: tiny);
                margin: 0rem;

                    @include media(map-get($breakpoints, base)){font-size: map-get($map: $font-size, $key: sm);}
        }
    }
}