//
//             1,   Components
//--------------------------------------------
@import "_helpers/mixins";
@import "_helpers/variables";
//--------------------------------------------

@mixin form--base-style{
    .form__input{@include input--base-styles;}

    .form__textarea{
        @include input--base-styles;
        height: 10rem;

            @include media(map-get($breakpoints, base)){height: 8rem;}
    }

    .form__button{@include button--base-style;}
}

@mixin input--base-styles{
    width: 100%;
    margin-top: 1rem;
    padding: 0.75rem 0.5rem;
    font-size: 18px;

        @include media(map-get($breakpoints, base)){
            margin-top: 0.4rem;
            font-size: 12px;
            padding: 0.3rem 0.2rem;
    }
}
