//
//               4,   Pages
//--------------------------------------------

.body--landing-page{

        main, div{
            @include size(100%, 100%);
            @include position(fixed);
        };

        .content--landing-page{
            background: map-get($map: $color, $key: primary) 
                        url('../images/bg-img2.jpg') 
                        no-repeat 
                        center;
            background-size: cover;
        };

        .landing-page__textbox{
            @include padding--custom(1rem, 1rem){padding-bottom: 2rem;};
            display: inline-block;
        
                a {
                    @include centerDiv--x;
                    text-decoration: none;
                    color: map-get($map: $grey, $key: white);
                    cursor: pointer;
                    font-family: 'Fugaz One', cursive;
                    text-transform: none;
                    
            };
        };

        .textbox__title{
            @include position(absolute, null, null, 10%, null);
            @include heading-styles--large{
                font-size: map-get($font-size, XL);
                line-height: 2.5rem;
            };
                @include media(map-get($breakpoints, base)){
                    bottom: 20%;
                }
        };

        .textbox__paragraph{
            @include position(absolute, 8%, null, null, null);
            @include heading-styles--large{
                font-size: map-get($font-size, L);
            };
                @include media(map-get($breakpoints, base)){
                    top: 82%;
                };
         };
};