//
//             1,   Components
//--------------------------------------------
@import "_helpers/mixins";
@import "_helpers/variables";

//--------------------------------------------

@mixin button--base-style{
    position: relative;
    float: right;
    background-color: map-get($map: $color, $key: primary);
    color: map-get($map: $grey, $key: white);
    border: none;
    padding: 0.5rem;
    margin-top: 0.5rem;

        @include media(map-get($breakpoints, base)){
            margin-top: 0.2rem;
            padding: 0.4rem;
        }
}