//
//               4,   Pages
//--------------------------------------------

.content--booking-page{
        color: map-get($map: $color, $key: primary);

        .booking-page__outer-container{
            padding: 2.5rem;

                @include media(map-get($breakpoints, large)){
                    @include centerDiv--x;
                    max-width: 1000px;
                    position: relative;
                    padding: 3rem 1rem 0rem 1rem;
            }
        }

        .outer-container__inner-title{
            text-transform: none;
            font-size: map-get($map: $font-size, $key: L);
            margin: 2rem 0rem;

                @include media(map-get($breakpoints, large)){text-align: center;}
    }

}

.booking-page__inner-container{
        padding: 2rem 0rem;
        border-bottom: 1px solid map-get($map: $grey, $key: base);

            @include media(map-get($breakpoints, large)){
                max-width: 33%;
                display: inline-block;
                text-align: center;
                border: none;
                padding: 0rem 0.5rem;
            }

    .inner-container__article{
        @include media(map-get($breakpoints, large)){
                overflow: hidden;
                height: 330px;
        }

        .article__el-container{
            @include media(map-get($breakpoints, large)){
                height: 350px;
                position: relative;
                top: 0%;
            }
        }

        .article__inner-image{
            @include size(100%, auto);
            opacity: 1;

                @include media(map-get($breakpoints, large)){max-height: 200px;}
            }
        }

        .article__inner-title{
            @include inner-article--text-style;
            text-transform: none;

                @include media(map-get($breakpoints, large)){
                    font-size: 18px;
                    margin-top: 1.5rem;

                    .icon-arrow-40{display: none;}
            }
        }

        .article__inner-text{
            @include inner-article--text-style;
                @include media(map-get($breakpoints, large)){padding-bottom: 2rem;}
        }

        p:last-child{margin: 1.5rem 0rem;}

        p[data-id='description']{margin-top: 0rem;}

        .inner-container__article-button{
            @include button--base-style;
            float: none;
            font-size: map-get($map: $font-size, $key: sm);
            padding: 1.2rem 1.5rem;

                @include media(map-get($breakpoints, large)){
                    padding: 1rem;
                    margin-top: 1rem;
        }
    }
}

.booking-page__inner-container:last-child{border-bottom: none;}

.text--animate{
    overflow: auto;
    height: 100%;
}

