//               4,   Pages
//--------------------------------------------


.content--portfolio-page{

    .portfolio__inner-container{
        background: #2c1728 no-repeat center;
        background-size: 150%;
        max-width: 25%;
        max-height: 90px;
        color: white;
        position: relative;
        display: inline-block;
        overflow: hidden;
        transition: max-height 0.5s;

            @include media(map-get($breakpoints, small)){
                max-height: 120px;

                @include media(map-get($breakpoints, base)){
                    max-height: 190px;

                    @include media(map-get($breakpoints, large)){
                        max-height: 230px;

                        @include media(map-get($breakpoints, huge)){
                            max-height: 300px;

                            @include media(map-get($breakpoints, maxSize)){
                                max-height: 375px;
                            }
                        }
                    }
                }
            }

        .inner-container__image{
            @include centerDiv--x;
            z-index: 0;
            display: block;
            position: relative;
            width: 250%;
        }

        .inner-container__icon-box{
            @include size(100%, 100%);
            position: absolute;
            bottom: 0px;
            display: block;
            z-index: 3;
        }

        .icon-heart-o{
            @include hover--darken;
            @include position(absolute, null, null, 1rem, 1.5rem);
            font-size: map-get($map: $font-size, $key: m);
            z-index: 5;
        }

        .icon-heart-1{
            @include position(absolute, null, null, 1rem, 1.5rem);
            font-size: map-get($map: $font-size, $key: m);
            color: red;
            z-index: 5;

            &:hover{
                color: red;
            }
        }

        .icon-box__likes{
            @include hover--darken;
            @include position(absolute, null, null, 1rem, 3.5rem);
            margin: 0px;
            font-size: map-get($map: $font-size, $key: base);
            z-index: 5;
        }

        .media__nav--icon-box{
            margin: 0px;
            list-style: none;
            position: relative;
            padding: 0px;
            left: 50%;
            top: 50%;
            display: inline-block;
            transform: translateY(-50%);
        }

        .media__nav__list-item{
            @include hover--darken;
            margin: 2rem 0rem;
        }

        .icon-external-link{
            @include hover--darken;
            @include position(absolute, null, 1.5rem, 1rem, null);
            font-size: map-get($map: $font-size, $key: m);
            z-index: 5;
        }
    }
}

.darken--bg{
    opacity: 0.5;
    transition: opacity 0.5s;
}