//
//               4,   Pages
//--------------------------------------------

.content{
    .wrapper__contact--img{background-image: url(../images/bg-img6.jpeg);}
}

.main-section--contact-page{
    p{
        font-size: map-get($map: $font-size, $key: base);
        line-height: 1.4;
        letter-spacing: 0px;
        margin-top: 2rem;

        @include media(map-get($breakpoints, large)){
            font-size: map-get($map: $font-size, $key: sm);
            line-height: 1.2rem;
            letter-spacing: 0px;
        };
    }
}

.main-section__form{@include form--base-style;}

