//      A: text related mixins
//------------------------------------------------

@mixin font-base-styles(){      // Grouped up styles for each text element
    
    h1 {@include heading-styles--large;}

    h3, h4 {@include heading-styles--base;}

    p, a, li, {@include text-styles--base;}
};


@mixin text-styles--custom($text-trans, $text-size, $font-family){
    font-family: $font-family;
    font-size: $text-size;
    text-transform: $text-trans;
    @content;
};

@mixin heading-styles--large(){
    @include text-styles--custom(
        $text-trans: none, 
        $text-size: map-get($font-size, XL),
        $font-family: $font-family-logo
    );
    @content;
};

@mixin heading-styles--base(){
    @include text-styles--custom(
        $text-trans: uppercase,
        $text-size: map-get($font-size, m),
        $font-family: $font-family-heading)
        {
            letter-spacing: 1px;
            font-weight: 300;
        };
    @content;
};

@mixin text-styles--base(){
    @include text-styles--custom(
        $text-trans: none,
        $text-size: map-get($font-size, m),
        $font-family: $font-family-text)
        {
            letter-spacing: 1px;
            line-height: 1.8rem;
        };
    @content;
};

@mixin list-styles--null{
    margin: 0px;
    padding: 0px;
    list-style: none;
};

//==========================================================

//          B:   padding & margin mixins
//--------------------------------------------

@mixin padding--custom($col, $row){
    padding: $col $row;
    @content;
};

@mixin margin--custom($col ,$row){
    margin: $col $row;
    @content;
}

@mixin box-model__maker($p-col, $p-row, $m-col, $m-row){    // A 'constuctor' for spacing box-model in divs, use to make presets.
    padding: $p-col $p-row;
    margin: $m-col $m-row;
    @content;
}

// presets



//===========================================================

//           C:   size mixin
//--------------------------------------------

@mixin size($width, $height){
    width: $width;
    height: $height;
    @content;
}

@mixin size--square($width, $height: $width){
    width: $width;
    height: $height;
    @content;
}

@mixin size--rectangle($width, $height: ($width / 2)){
    width: $width;
    height: $height;
    @content;
}

//=============================================================

//             D:   media queries
//--------------------------------------------

@mixin media($breakpoint){

    @media screen and (min-width: $breakpoint){
        @content;
    };
};

//==============================================================

//             E:   position mixin
//--------------------------------------------

@mixin position($position: relative, $top: 0px, $right: 0px, $bottom: 0px, $left: 0px){
    position: $position;
    top: $top;
    right: $right;
    bottom: $bottom;
    left: $left;
    @content;
};

@mixin position--centerDiv($position: relative, $top: 0px, $right: 0px, $bottom: 0px, $left: 0px){
    position: $position;
    top: $top;
    right: $right;
    bottom: $bottom;
    left: $left;
    margin: auto;
    @content;
};

@mixin centerDiv--x(){
    left: 50%;
    transform: translateX(-50%);
};

//==============================================================

//             F:   element spercfic mixins
//--------------------------------------------

@mixin nav--inline-style{               // header__nav--inline 
    font-size: map-get($map: $font-size, $key: tiny);
    letter-spacing: 0px;
    line-height: normal;
    text-decoration: none;
    color: map-get($map: $color, $key: primary);
    margin-left: 10px;

        @include media(map-get($breakpoints, large)){
            font-size: map-get($map: $font-size, $key: sm);
            margin-left: 20px;
                @include media(map-get($breakpoints, huge)){
                    margin-left: 25px;
        };
    };
};

@mixin inner-article--text-style{       //main use in Booking page
    font-size: map-get($map: $font-size, $key: base);
    line-height: 1.5rem;
    letter-spacing: 0px;
    margin: 1rem 0rem;
};

@mixin hover--darken{
    &:hover{
        color: lightgray;
        cursor: pointer;
    }
};
