//
//               4,   Pages
//--------------------------------------------

.content {

    .content__wrapper {
        background: white  
                    no-repeat 
                    center;
        background-size: cover;
        position: relative;
        height: 40rem;
        padding-top: 25rem;

            @include media(map-get($breakpoints, small)){ padding-top: 11rem;
                
                    @include media(map-get($breakpoints, base)){ padding: 0px;

                            @include media(map-get($breakpoints, huge)){ height: 43rem;
                }
            }
        }
    }

    .wrapper__about--img{
        background-image: url(../images/bg-img1.jpg);
    }

    .main-section {
        background-color: white;
        padding: 2rem 2rem;
        max-width: 90%;
        float: right;
        position: relative;
        color: map-get($map: $color, $key: primary);

            @include media(map-get($breakpoints, small)){ max-width: 80%;

                @include media(map-get($breakpoints, base)){
                    float: none;
                    max-width: 50%;
                    left: 50%;
                    top: 8%;
                    transform: translate(-10%);
                    padding: 3rem;
                    color: map-get($map: $color, $key: primary--light);

                        @include media(map-get($breakpoints, huge)){
                            width: 450px;
                            max-width: 30%;
                            left: 56%;
                }
            }
        }

            .main-section__title{
                font-size: 32px;
                font-weight: normal;
                margin: 1.5rem 0rem;
                letter-spacing: 1px;
                line-height: 1.5rem;

                    @include media(map-get($breakpoints, base)){
                        margin-top: 0px;
                        font-size: 26px;
                }
            }

            .main-section__text{
                font-size: 14px;
                margin: 1rem 0rem;
                letter-spacing: 1px;
                line-height: 1.5rem;

                    @include media(map-get($breakpoints, base)){
                        letter-spacing: 0px;
                        line-height: 1.3rem;
            }
        }
    }
}

