//
//               4,   Pages
//--------------------------------------------

.img-viewer{
    @include size(100%, 100%);
    background-color: map-get($map: $grey, $key: white);
    position: fixed;
    z-index: 6;
}

.header--view-page{
    padding: 0rem;

        @include media(map-get($breakpoints, base)){padding: 0rem 1rem 3rem 1rem;}

    .header__nav{
        text-align: left;
        padding: 2rem;
        color: map-get($map: $color, $key: primary--light);

            @include media(map-get($breakpoints, base)){padding: 3rem 1rem 1.5rem 1rem;}
    }

    .nav__list--like-icons{
        padding: 0px;
        margin-bottom: 2rem;
        display: block;

            @include media(map-get($breakpoints, base)){
                margin: 0px;
                display: inline-block;
            }

            .icon-heart-o{
                display: inline-block;
                
                    @include media(map-get($breakpoints, base)){margin-right: 5rem;}
            }

            .icon-external-link{
                display: none;

                    @include media(map-get($breakpoints, base)){
                        margin-right: 1rem;
                        display: inline-block;
            }
        }
    }

    .nav__list-item{
        display: inline-block;
        font-size: map-get($map: $font-size, $key: base);
        cursor: pointer;
    }

    .media__nav--header{
        display: inline-block;
        float: right;

            @include media(map-get($breakpoints, base)){display: none;}
    }

    .media__nav__list-item{
        margin: 0rem 0rem 0rem 2rem;
        cursor: pointer;
        display: inline;
        font-size: map-get($map: $font-size, $key: L);

            @include media(map-get($breakpoints, base)){font-size: map-get($map: $font-size, $key: m);}
    }

    .nav__list--control-icons{
        padding: 0px;
        margin: 0px;
        display: block;
        position: relative;
        text-align: center;

            @include media(map-get($breakpoints, base)){
                float: right;
                display: inline-block;
            }

        li{
            font-size: map-get($map: $font-size, $key: sm);
            margin-left: 3rem;
            cursor: pointer;
        }

        .icon-arrow-67{
            margin-left: 0rem;

                @include media(map-get($breakpoints, base)){margin-left: 3rem;}
        }

        .icon-arrow-68{
            margin-left: 5rem;

                @include media(map-get($breakpoints, base)){margin-left: 3rem;}
        }

        .icon-delete-1{
            margin: 0px;
            float: right;

                @include media(map-get($breakpoints, base)){
                    margin-left: 5rem;
                    float: none;
            }
        }
    }
}

.image-viewer{
    @include centerDiv--x;
    width: 90%;
    position: relative;

        @include media(map-get($breakpoints, base)){width: 75%;}

        img{
            @include centerDiv--x;
            position: relative;
            width: 100%;
        }
}