//
//             2,    Helpers
//--------------------------------------------
/*
    =============   Contents   =============

             A:   font-family
             B:   font-sizes
             C:   padding & margin
             D:   breakpoints
             E:   color palette


    ========================================
*/

//          A: font-family

$font-family-text: 'Raleway', sans-serif;
$font-family-heading: 'Roboto', sans-serif;
$font-family-logo: 'Fugaz One', cursive;

//          B: font-sizes

$font-size-body: 16px;
$font-size-base: 16px;

$font-size: (
    XXL: ($font-size-base * 3),
    XL: (($font-size-base * 2) + 4),
    L: ($font-size-base * 1.5),
    m: ($font-size-base + 2),  
    base: $font-size-base,
    sm: ($font-size-base - 2),
    tiny: ($font-size-base - 4)
);

//=======================================================

//          C: padding & margin
                        
$padding: (
    Xlarge: 2rem,
    large: 1.5rem,
    base: 1rem,
    small: 0.5rem,
    tiny: 0.25rem
);

$margin: (
    Xlarge: 2rem,
    large: 1.5rem,
    base: 1rem,
    small: 0.5rem,
    tiny: 0.25rem
);

//=======================================================

//          D: breakpoints

$breakpoints: (
    maxSize: 1500px,    
    huge: 1200px,
    large: 920px,
    base: 760px,
    small: 480px
);

//========================================================

//          E: color palette

$color: (
    primary: #3d2037,
    primary--dark: darken(#3d2037, 5%),
    primary--light: lighten(#3d2037, 10%),
    secondary: darken(#7cc1a4, 5%),
    third: lighten(#ffeddb, 2%),
);

$grey: (
  white: white,
    lightgrey: lighten(grey, 20%),
    base: grey,
    darkgrey: darken(grey, 20%),
    black: black
);
