//
//             1,   Components
//--------------------------------------------
@import "_helpers/mixins";
@import "_helpers/variables";

//      Navigation
//============================================

    .header__nav--burger-menu{          // Burger menu navigation
        @include size(100%, 100%);
        @include position(fixed, 0rem, null, null, 0rem);
        display: block;
        background: white;
        z-index: 10;
        
        .nav__list{                     // <ul> tag
            @include list-styles--null;
            @include position(relative, 25%, null, null, 50%);
            transform: translate(-50%);
            text-align: center;
        }

        .nav__list-item{                // <li> tags
            text-decoration: none;
            color: map-get($map: $color, $key: secondary);
            font-size: map-get($map: $font-size, $key: L);
            letter-spacing: 2px;

            .list-item__separator{      // <div> for small line
                border-top: solid 2px map-get($map: $grey, $key: lightgrey);
                display: block;
                margin: 10px 20px;
                width: 20px;
                position: relative;
                left: 50%;
                transform: translateX(-150%);
                opacity: 0.6;
            }
        }

        .nav__list-item--burger-link{   // <a> tags
            color: map-get($map: $color, $key: primary);
            opacity: 0.6;

                &:hover{color: map-get($map: $color, $key: secondary)};
        }
    }

    .media__nav--header{            // Basic nav for social media links (icons)
        @include list-styles--null;
        text-align: right;
        clear: right;
        color: map-get($map: $color, $key: primary);

            @include media(map-get($breakpoints, base)){
                display: inline-block;
            };

        .media__nav__list-item{
            display: inline;
            font-size: 28px;
            margin-left: 15px;

            @include media(map-get($breakpoints, base)){
                font-size: map-get($map: $font-size, $key: base);
                margin-left: 10px;
            };
        }
    }

    .header__nav--inline{           // Standard inline navigation for large screens
        display: none;

            @include media(map-get($breakpoints, base)){display: inline-block;};

            .nav__list{                            // <ul> tag
                @include list-styles--null;
            }

            .nav__list-item{                       // <li> tags
                @include nav--inline-style;
                color: map-get($map: $color, $key: secondary);
                display: inline;     
            }

            .nav__list-item--inline-link{           // <a> tags
                @include nav--inline-style;

                    &:first-child{margin-left: 0px};

                    &:hover{color: map-get($map: $color, $key: secondary)};
        }
    }